.wp-block-zolo-fancy-list-child {
    --zolo-item-gap: 15px;
    --zolo-item-padding: 25px;
    --zolo-content-gap: 10px;
    --zolo-title-margin: 0;
    --zolo-title-text-color: #06031d;
    --zolo-desc-text-color: #595760;
    --zolo-icon-padding: 10px;
    --zolo-number-padding: 7px 17px;
    --zolo-icon-size: 16px;
    --zolo-img-size: 50px;

    display: flex;
    align-items: center;
    gap: var(--zolo-item-gap);
    background-color: var(--zoloblocks-item-bg-color);
    border-width: var(--zolo-item-border-width);
    border-style: var(--zolo-item-border-style);
    border-color: var(--zoloblocks-item-border-color);
    justify-content: space-between;
    padding: var(--zolo-item-padding);
    box-sizing: border-box;
    border-radius: var(--zolo-item-border-radius);
    transition: background 0.2s;

    .zb-fancy-list-content {
        display: flex;
        align-items: center;
        gap: var(--zolo-content-gap);
        flex: 1;
    }

    .zb-fancy-list-inner-content {
        flex: 1;
    }

    .zb-fancy-list-title {
        margin: var(--zolo-title-margin);
        font-size: 20px;
        color: var(--zolo-title-text-color);
    }

    .zb-fancy-list-text {
        font-size: 14px;
        color: var(--zolo-desc-text-color);
        font-weight: 400;
    }

    .zb-fancy-icon,
    .zb-fancy-list-number {
        display: flex;
        align-items: center;
        justify-content: center;
        color: var(--zoloblocks-brand-button-color);
        background-color: var(--zoloblocks-brand-color);
        border-radius: var(--zoloblocks-button-border-radius);
        transition: all 0.3s ease-in-out;
    }

    .zb-fancy-icon {
        padding: var(--zolo-icon-padding);
    }

    .zb-fancy-list-number {
        padding: var(--zolo-number-padding);
    }

    .zb-fancy-icon svg {
        width: var(--zolo-icon-size);
        height: var(--zolo-icon-size);
        fill: var(--zoloblocks-brand-button-color);
        display: block;
    }

    .zb-fancy-list-number {
        box-sizing: border-box;
        border-radius: var(--zoloblocks-button-border-radius);
    }

    .zb-fancy-list-image {
        width: var(--zolo-img-size);
        height: var(--zolo-img-size);
        border-radius: var(--zolo-item-border-radius);
        overflow: hidden;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            display: block;
        }
    }

    &.fancy-list-align-right{
        flex-direction: row-reverse;
        .zb-fancy-list-item,
        .zb-fancy-list-content {
            flex-direction: row-reverse;
            text-align: right;
        }
    }
}
